:root {
  --text-color: #1e1e1e;
  --text-color-secondary: rgba(0, 0, 0, 0.5);
  --theme: #fa8686;
  --border: 1px solid #f2f2f2;
  --header-height: 56px;
  --input-focus-border: 1px solid #fa8686;
}

#root {
  margin: 0;
  padding: 0;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: Monaco, Consolas, source-code-pro, Menlo, 'Courier New',
    monospace;
  font-weight: 500;
  font-size: 0.75em;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0;
  padding: 0;
  color: var(--text-color);
}

h1 {
  font-weight: 600;
  font-size: 1.125em;
  line-height: 1.125em;
  letter-spacing: -0.03em;
}

h2 {
  font-weight: 600;
  font-size: 0.875em;
  line-height: 0.875em;
  letter-spacing: -0.03em;
}

p,
span {
  font-weight: 440;
  font-size: 1em;
  line-height: 1em;
  letter-spacing: -0.02em;
}

button {
  height: 30px;
  background: transparent;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #1e1e1e;
  font: unset;
  font-size: 14px;
  letter-spacing: -0.002em;
  font-weight: 600;
  display: flex;
  align-items: center;
  padding: 0px 4px;
}

button:hover {
  cursor: pointer;
  background: #f2f2f2;
}

button:focus {
  outline: none;
  border: 1px solid #fa8686;
}

button:active {
  background-color: #e5e5e5;
}

select {
  height: 30px;
  font: unset;
  font-family: 'Inter';
  font-weight: 400;
  font-size: 0.875em;
  color: var(--text-color);
  letter-spacing: -0.02em;
  border: 1px solid #e5e5e5;
  padding: 0 4px;
  box-sizing: border-box;
}

select:hover {
  cursor: pointer;
}

select:focus {
  outline: none;
  border: 1px solid #fa8686;
}

@media screen and (min-width: 1024px) {
  input {
    font-size: 0.875em;
  }
}
